* {
  /*all elements to inherit same font*/
  font-family: "Montserrat", sans-serif;
}

.Profile{
  text-align: center;
  margin-top: 50px;
}
.Profile input {
  height: 7%;
  width: 50%;
  border: 1px solid #000;
  text-align: left;
  border-radius: 3px;
}

.Profile textarea {
  height: 7%;
  width: 40%;
  border: 1px solid #000;
  text-align: left;
  border-radius: 3px;
}

.Profile h1 {
  font-weight: bold;
  font-size: 30px;
  color: black;
  text-align: center;
}

.Profile p {
  font-size: 20px;
  font-weight: bold;
  color: black;
}

.profilepic {
  margin-left: 20%;
  min-width: 400px;
}

.profilepic img {
  object-fit: cover;
  border-radius: 50%;
  width:300px;
  height:300px;
}

.updatedMessage {
  text-align: center;
}

.updateProfileButton input[value="Save Profile"] {
  color: white;
  letter-spacing: 1px;
  background-color: #3680a3;
  border: 3px solid #3680a3;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  width: 20%;
  min-width: 150px;
  margin-top: 20px;
  margin-right: 30%;
  text-align: center;
  margin-left: 30%;
}

