}

.removepic {
  color: white;
  letter-spacing: 1px;
  background-color: #7e7e7e;
  border: 3px solid #7e7e7e;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  width: 20%;
  min-width: 150px;
  margin-top: 20px;
  text-align: center;
}

.profilepic input[type="file"] {
  color: white;
  letter-spacing: 1px;
  background-color: #7e7e7e;
  border: 3px solid #7e7e7e;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  width: 80%;
  margin-top: 20px;
  text-align: center;
}

.Profile input[type="text"] {
  width: 50%;
}

.Profile textarea {
  width: 50%;
  height: 100px;
}

.Profile input[type="date"],
input[value="submit"] {
  width: 50%;
}

